import { useState, useEffect } from 'react';
import { supabase, Post } from '../lib/supabase';
import { useAuth, useUser } from '@clerk/clerk-react';

export const usePosts = () => {
  const [posts, setPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { userId } = useAuth();
  const { user } = useUser();

  const fetchPosts = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase
        .from('posts')
        .select('*')
        .order('created_at', { ascending: false });

      if (error) throw error;
      setPosts(data || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : '게시글을 불러오는데 실패했습니다.');
    } finally {
      setLoading(false);
    }
  };

  const createPost = async (postData: {
    title: string;
    content: string;
    golf_course: string;
    region: string;
  }) => {
    try {
      if (!userId) throw new Error('로그인이 필요합니다.');

      const { data, error } = await supabase
        .from('posts')
        .insert([{
          ...postData,
          author_id: userId,
          author_name: user?.fullName || user?.firstName || '익명',
          views: 0
        }])
        .select()
        .single();

      if (error) throw error;
      
      setPosts(prev => [data, ...prev]);
      return { success: true };
    } catch (err) {
      return { success: false, error: err instanceof Error ? err.message : '게시글 작성에 실패했습니다.' };
    }
  };

  const incrementViews = async (postId: string) => {
    try {
      const currentPost = posts.find(p => p.id === postId);
      const newViews = (currentPost?.views || 0) + 1;
      
      const { error } = await supabase
        .from('posts')
        .update({ views: newViews })
        .eq('id', postId);

      if (error) throw error;

      setPosts(prev => prev.map(post => 
        post.id === postId ? { ...post, views: newViews } : post
      ));
    } catch (err) {
      console.error('조회수 증가 실패:', err);
    }
  };

  const getPostsByRegion = (region: string) => {
    return posts.filter(post => post.region === region);
  };

  useEffect(() => {
    fetchPosts();
  }, []);

  return {
    posts,
    loading,
    error,
    createPost,
    incrementViews,
    getPostsByRegion,
    refetch: fetchPosts
  };
};

