import { useState, useEffect } from 'react';
import { supabase, Round } from '../lib/supabase';
import { useAuth, useUser } from '@clerk/clerk-react';

export const useRounds = () => {
  const [rounds, setRounds] = useState<Round[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { userId } = useAuth();
  const { user } = useUser();

  const fetchRounds = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase
        .from('rounds')
        .select('*')
        .order('round_date', { ascending: false });

      if (error) throw error;
      setRounds(data || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : '라운드 정보를 불러오는데 실패했습니다.');
    } finally {
      setLoading(false);
    }
  };

  const createRound = async (roundData: {
    title: string;
    description: string;
    golf_course: string;
    date: string;
    time: string;
    max_participants: number;
    green_fee?: string;
    region: string;
  }) => {
    try {
      if (!userId) throw new Error('로그인이 필요합니다.');

      const { data, error } = await supabase
        .from('rounds')
        .insert([{
          ...roundData,
          round_date: roundData.date,
          green_fee: roundData.green_fee ? parseInt(roundData.green_fee) : undefined,
          score: 0,
          weather: '',
          green_condition: 0,
          fairway_condition: 0,
          bunker_condition: 0,
          current_participants: 1,
          status: 'recruiting',
          user_id: userId,
          user_name: user?.fullName || user?.firstName || '익명',
          author_id: userId,
          author_name: user?.fullName || user?.firstName || '익명'
        }])
        .select()
        .single();

      if (error) throw error;
      
      setRounds(prev => [data, ...prev]);
      return { success: true };
    } catch (err) {
      return { success: false, error: err instanceof Error ? err.message : '라운드 생성에 실패했습니다.' };
    }
  };

  const updateRound = async (roundId: string, updates: Partial<Round>) => {
    try {
      const { data, error } = await supabase
        .from('rounds')
        .update(updates)
        .eq('id', roundId)
        .select()
        .single();

      if (error) throw error;
      
      setRounds(prev => prev.map(round => 
        round.id === roundId ? { ...round, ...data } : round
      ));
      
      return data;
    } catch (err) {
      throw new Error(err instanceof Error ? err.message : '라운드 수정에 실패했습니다.');
    }
  };

  const deleteRound = async (roundId: string) => {
    try {
      const { error } = await supabase
        .from('rounds')
        .delete()
        .eq('id', roundId);

      if (error) throw error;
      
      setRounds(prev => prev.filter(round => round.id !== roundId));
    } catch (err) {
      throw new Error(err instanceof Error ? err.message : '라운드 삭제에 실패했습니다.');
    }
  };

  const getRoundsByUser = (userId: string) => {
    return rounds.filter(round => round.user_id === userId);
  };

  const getRoundsByRegion = (region: string) => {
    return rounds.filter(round => round.region === region);
  };

  const getAverageScore = (userId?: string) => {
    const userRounds = userId ? getRoundsByUser(userId) : rounds;
    if (userRounds.length === 0) return 0;
    
    const totalScore = userRounds.reduce((sum, round) => sum + round.score, 0);
    return Math.round(totalScore / userRounds.length);
  };

  const joinRound = async (roundId: string) => {
    try {
      if (!userId) throw new Error('로그인이 필요합니다.');

      const round = rounds.find(r => r.id === roundId);
      if (!round) throw new Error('라운드를 찾을 수 없습니다.');
      
      if (round.current_participants >= round.max_participants) {
        throw new Error('모집이 완료된 라운드입니다.');
      }

      const { error } = await supabase
        .from('round_participants')
        .insert([{ round_id: roundId, user_id: userId }]);

      if (error) throw error;

      // 참가자 수 업데이트
      const { error: updateError } = await supabase
        .from('rounds')
        .update({ 
          current_participants: round.current_participants + 1,
          status: round.current_participants + 1 >= round.max_participants ? 'full' : 'recruiting'
        })
        .eq('id', roundId);

      if (updateError) throw updateError;

      // 로컬 상태 업데이트
      setRounds(prev => prev.map(r => 
        r.id === roundId 
          ? { 
              ...r, 
              current_participants: r.current_participants + 1,
              status: r.current_participants + 1 >= r.max_participants ? 'full' : 'recruiting'
            }
          : r
      ));

      return { success: true };
    } catch (err) {
      return { success: false, error: err instanceof Error ? err.message : '라운드 참가에 실패했습니다.' };
    }
  };

  const leaveRound = async (roundId: string) => {
    try {
      if (!userId) throw new Error('로그인이 필요합니다.');

      const { error } = await supabase
        .from('round_participants')
        .delete()
        .eq('round_id', roundId)
        .eq('user_id', userId);

      if (error) throw error;

      const round = rounds.find(r => r.id === roundId);
      if (round) {
        const { error: updateError } = await supabase
          .from('rounds')
          .update({ 
            current_participants: Math.max(0, round.current_participants - 1),
            status: 'recruiting'
          })
          .eq('id', roundId);

        if (updateError) throw updateError;

        setRounds(prev => prev.map(r => 
          r.id === roundId 
            ? { 
                ...r, 
                current_participants: Math.max(0, r.current_participants - 1),
                status: 'recruiting'
              }
            : r
        ));
      }

      return { success: true };
    } catch (err) {
      return { success: false, error: err instanceof Error ? err.message : '라운드 탈퇴에 실패했습니다.' };
    }
  };

  const getRoundsByStatus = (status: string) => {
    if (status === '전체') return rounds;
    
    const statusMap: { [key: string]: string } = {
      '모집중': 'recruiting',
      '모집완료': 'full',
      '완료': 'completed'
    };
    
    return rounds.filter(round => round.status === statusMap[status]);
  };

  const isUserParticipating = (roundId: string) => {
    // 실제로는 round_participants 테이블을 확인해야 하지만
    // 여기서는 간단히 구현
    return false;
  };

  useEffect(() => {
    fetchRounds();
  }, []);

  return {
    rounds,
    loading,
    error,
    createRound,
    updateRound,
    deleteRound,
    joinRound,
    leaveRound,
    getRoundsByUser,
    getRoundsByRegion,
    getRoundsByStatus,
    isUserParticipating,
    getAverageScore,
    refetch: fetchRounds
  };
};

